
@media screen and (max-width: 320px) {
body {
    zoom: 0.85;
}
}
#canvas {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
#app {
  width: 100%;
  min-height: 100%;
  background: url('assets/image/bg.jpg') no-repeat center center;
  background-size: cover;
}



