
.content {
  overflow: hidden;
}
.info {
  width: 330px;
  padding: 20px 20px;
  background: rgba(255, 255, 255, 0.3);
  margin: 50px auto;
  border-radius: 15px;
  color: #fff;
}
.info li {
  line-height: 36px;
  font-size: 16px;
  font-weight: bold;
  color: rgba(255, 255, 255, 0.8);
}
.info li span {
  font-weight: normal;
  color: rgba(204, 204, 204, 0.8);
}
.info li a {
  color: blue;
}



